import React, { lazy, Suspense } from 'react';
import { connect } from "react-redux";
import { Row, Col, Form, FormControl, Button } from 'react-bootstrap';
import { Navigate } from 'react-router-dom';
import Layout from '../utils/layout';
import {
    GET_CUSTOMER_LIST_ACTION,
    GET_CUSTOMER_DETAIL_ACTION,
    UPDATE_CUSTOMER_YEAR_ACTION,
} from "../../redux/actions/actions"
// import CustomerTop from "./top"

import SpinnerComponent from '../utils/spinner';
// import CustomerMonth from './charts/customer_month';
// import CustomerSelect from '../utils/select/customer_select';
// import SalesDateSelector from '../sales/sales_date_selector';
// import SalesTable from '../sales/sales_table';

const CustomerMonth = lazy(() => import('./charts/customer_month'));
const CustomerSelect = lazy(() => import('../utils/select/customer_select'));
const SalesDateSelector = lazy(() => import('../sales/sales_date_selector'));
const SalesTable = lazy(() => import('../sales/sales_table'));
const CustomerTop = lazy(() => import('./top'))

const getCustomers = (sales, customer) => {
    sales = sales.filter(function (or) {
        return or.customer.name === customer.name
    });

    return sales
}
class CustomerPage extends React.Component {
    state = {
        redirect: false,
        spinner: false,
        customer_spinner: false,
        didnt_load: false,
        customer_list: this.props.customer_list ? this.props.customer_list : [],
        customer: this.props.customer_list ? this.props.customer_list[0] : null,
        customer_name: null,
        customer_id: null,
        dashboard_metric: this.props.dashboard_metric,
        currency_type: this.props.currency_type,
        customer_year: this.props.customer_year,

        sales: [],
    }

    static getDerivedStateFromProps = async (props, state) => {
        const { token, customer_list, customer_detail } = props
        if (token === undefined || token === "") {
            state.redirect = true
            return state
        }
        else {

            if (state.customer_id === null || customer_detail === undefined) {

                state.customer_id = customer_list[0].id
                state.spinner = false
                state.customer_spinner = true
                await props.GET_CUSTOMER_DETAIL_ACTION(customer_list[0].id)
            }
            else if (customer_detail !== undefined && state.customer_id == customer_detail.id) {

                state.customer = customer_detail
                state.sales = props.sales ? getCustomers(props.sales, customer_detail) : []
                state.customer_spinner = false
                state.spinner = false
            }
            else if (customer_detail !== undefined && state.customer_id !== customer_detail.id) {
                state.customer_spinner = true
            }
            else {
                state.spinner = false
                state.customer_spinner = false
                state.customer_id = props.customer_detail.id
            }
        }

        state.customer_list = customer_list
        state.dashboard_metric = props.dashboard_metric
        state.currency_type = props.currency_type
        state.customer_year = props.customer_year

        return state
    }
    onSearchChange = (e) => {

        if (e === "" || e === undefined || e === null) {
            this.setState({ customer_list: this.props.customer_list })
        } else {
            this.setState({ customer_name: e })
        }
    }

    onSearch = (e) => {
        e.preventDefault()
        const { customer_name } = this.state
        let customer_list = this.props.customer_list
        customer_list = customer_list.filter(function (or) {
            return or.name.toLowerCase().includes(customer_name.toLowerCase())
        });


        this.setState({ customer_list: customer_list })
    }

    onClick = async (index) => {
        let customer_list = this.state.customer_list
        customer_list = customer_list.filter(function (or) {
            return or.id === parseInt(index)
        });

        if (customer_list.length > 0) {
            await this.setState({ customer_id: customer_list[0].id, customer_spinner: true })
            await this.props.GET_CUSTOMER_DETAIL_ACTION(customer_list[0].id)
        }

    }
    componentDidMount = async () => {
        this.setState({ spinner: true })
        await this.props.UPDATE_CUSTOMER_YEAR_ACTION(new Date().getFullYear())
    }


    render() {
        const { spinner, customer_year } = this.state
        let customer_data = null

        try {
            customer_data = this.state.dashboard_metric ? this.state.customer.revenue : this.state.customer.quantity
        } catch (e) {

        }

        if (this.state.redirect) {
            return <Navigate to={"/login"} />
        }

        return (
            <Layout header_title={"CUSTOMERS"}>
                {(spinner || this.props.customer_list === undefined || this.props.customer_detail === undefined) ? <SpinnerComponent spinner={spinner} /> : null}
                {this.props.customer_list !== undefined ?
                    <div>
                        <div>
                            <Suspense fallback={<SpinnerComponent spinner={true} />}>
                                <CustomerSelect />
                            </Suspense>
                        </div>
                        <Row>
                            <Col xs={3}>
                                <div className="customer-side chart-area">
                                    <h6>Pick a customer</h6>

                                    <Form className="d-flex" onSubmit={this.onSearch}>
                                        <FormControl
                                            type="search"
                                            placeholder="Search"
                                            className="me-2"
                                            aria-label="Search"
                                            size='sm'
                                            onChange={(e) => this.onSearchChange(e.target.value)}
                                        />
                                        <Button type="submit" variant="outline-primary" size="sm">Search</Button>
                                    </Form>

                                    <div>
                                        {this.state.customer_list.map((customer, idx) =>
                                            <div key={idx} className={"customer-side__scroll"}>
                                                {this.state.customer_id === customer.id ?

                                                    <Button className='customer-side__customer customer-side__active' variant="transparent" onClick={() => this.onClick(customer.id)}>

                                                        <div className='customer-side__info'>
                                                            <div className='customer-side__profile'>

                                                            </div>
                                                            <div>
                                                                <h6 className='customer-side__name'>{customer.name}</h6>
                                                                {/* <p className='customer-location'>{customer.location}</p> */}
                                                            </div>
                                                        </div>
                                                    </Button> :
                                                    <Button className='customer-side__customer' variant="transparent" onClick={() => this.onClick(customer.id)}>

                                                        <div className='customer-side__info'>
                                                            <div className='customer-side__profile'>

                                                            </div>
                                                            <div>
                                                                <h6 className='customer-side__name'>{customer.name}</h6>
                                                                {/* <p className='customer-location'>{customer.location}</p> */}
                                                            </div>
                                                        </div>
                                                    </Button>}
                                            </div>
                                        )}


                                    </div>
                                </div>

                            </Col>
                            <Col xs={9} style={{ position: "relative" }}>
                                {this.state.customer_spinner || customer_data === undefined ? <SpinnerComponent spinner={this.state.customer_spinner} /> :
                                    <div>
                                        <Suspense fallback={<SpinnerComponent spinner={true} />}>
                                            <CustomerTop
                                                customer={customer_data}
                                                name={this.state.customer ? this.state.customer.name : null}
                                                customer_year={customer_year}
                                            />
                                        </Suspense>
                                        <Suspense fallback={<SpinnerComponent spinner={true} />}>
                                            <CustomerMonth
                                                customer={customer_data}
                                                customer_year={customer_year}
                                                dashboard_metric={this.state.dashboard_metric}
                                                currency_type={this.state.currency_type}
                                            />
                                        </Suspense>

                                        <div>
                                            <Suspense fallback={<SpinnerComponent spinner={true} />}>
                                                <SalesDateSelector />
                                            </Suspense>
                                            <Suspense fallback={<SpinnerComponent spinner={true} />}>
                                                <SalesTable sales_list={this.state.sales} from_customer={true} />
                                            </Suspense>
                                        </div>

                                    </div>}


                            </Col>
                        </Row>
                    </div> : null
                }
            </Layout>
        )
    }
}

const mapStateToProps = state => {
    return {
        token: state.auth_reducer.token,
        customer_list: state.list_reducer.customer_list,
        customer_detail: state.list_reducer.customer_detail,
        dashboard_metric: state.update_reducer.dashboard_metric,
        currency_type: state.update_reducer.currency_type,
        customer_year: state.update_reducer.customer_year,

        sales: state.list_reducer.sales_list,
    };
};

const mapDispatchToProps = {
    GET_CUSTOMER_LIST_ACTION,
    GET_CUSTOMER_DETAIL_ACTION,
    UPDATE_CUSTOMER_YEAR_ACTION,

};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CustomerPage);
